$green: #24aa98;
$blue: #65abd0;
$grey: #c6ccd2;
$black: #383737;
$white: #fff;

$medium-font: 16px;

.c-steps-pagination {
	padding: 36px 120px;
	display: flex;
	justify-content: space-around;
}

.c-steps {
	width: 50%;
}

.c-steps-one {
	margin-bottom: 15px;
	a {
		font-weight: bold;
		font-size: $medium-font;
		color: $black;
		text-decoration: none;
		text-align: center;
		display: inline-block;
		width: 36px;
		height: 36px;
		margin: 5px;
		padding: 7px;
		border-radius: 100%;
		border: 2px solid $grey;
	}
	a:not(:last-child) {
		margin-right: 160px;
		position: relative;
		&:after {
			content: '';
			width: 171px;
			height: 2px;
			background-color: $grey;
			position: absolute;
			top: 50%;
			left: 100%;
			pointer-events: none;
			cursor: default;
		}
	}
	a.disabled {
		pointer-events: none;
		cursor: default;
	}
	a.active {
		color: $white;
		background-color: $green;
		border: none;
		padding: 8px;
		&:before {
			content: '';
			width: 90px;
			height: 4px;
			border-radius: 2px;
			background-color:$green;
			position: absolute;
			top: 48%;
			left: 99%;
			z-index: 1;
		}
	}
}

.c-steps-two {
	a {
		font-weight: bold;
		font-size: $medium-font;
		color: $black;
		text-decoration: none;
		text-align: center;
		display: inline-block;
		width: 36px;
		height: 36px;
		margin: 5px;
		padding: 7px;
		border-radius: 100%;
		border: 2px solid $grey;
	}
	a:not(:last-child) {
		margin-right: 160px;
		position: relative;
		&:after {
			content: '';
			width: 171px;
			height: 2px;
			background-color: $grey;
			position: absolute;
			top: 50%;
			left: 100%;
			pointer-events: none;
			cursor: default;
		}
	}
	a.disabled {
		pointer-events: none;
		cursor: default;
	}
	a.active {
		color: $white;
		background-color: $blue;
		border: none;
		padding: 8px;
	}
}

.c-pagination {
	width: 50%;
}

.c-pagination--circle {
	margin-bottom: 15px;
	a {
		display: inline-block;
		color: $black;
		font-size: $medium-font;
		font-weight: bold;
		width: 36px;
		text-align: center;
		height: 36px;
		margin: 5px;
		padding: 8px;
		text-decoration: none;
	}
	a.back {
		width: 50px;
		padding: 0;
	}
	a.next {
		width: 50px;
		padding: 0;
	}
	span {
		display: inline-block;
		color: $black;
		font-size: $medium-font;
		font-weight: bold;
		padding: 5px;
	}
	a.active {
		color: $white;
		background-color: $blue;
		border-radius: 100%;
		border: none;
	}
	a:hover:not(.active):not(.back):not(.next) {
		border-radius: 100%;
		border: 2px solid $grey;
		padding: 7px;
	}
}

.c-pagination--simple {
	text-align: center;
	a {
		display: inline-block;
		color: $grey;
		font-size: $medium-font;
		font-weight: bold;
		text-align: center;
		margin: 5px;
		text-decoration: none;
	}
	span {
		display: inline-block;
		color: $grey;
		font-size: $medium-font;
		font-weight: bold;
		margin: 5px;
	}
	a.active {
		color: $black;
	}
	a:hover:not(.active) {
		color: $black;
	}
}
